.app-wrapper {
    margin: auto;
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
    grid-template-areas: "h h" "n c";
}

.app-wrapper-content {
    grid-area: c;
    background-color: burlywood;
}

.app-wrapper-content img {
    height: 100px;
}